<template>
  <div class="box" @keyup="herospin" @keydown="heromove" tabindex="0"> </div>
  <div class="banner" :style="{ position: 'relative', top: `${16 * m}px` }">
    <div class="container">
      <div class="row" v-for="i in n" :key="i">
        <div class="col" v-for="j in m" :key="j">
          <img :src="imgbackground" />
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row" v-for="i in n" :key="i">
        <div class="col" v-for="j in m" :key="j">
          <img :src="images[map[j - 1][i - 1]]" />
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row" v-for="i in n" :key="i">
        <div class="col" v-for="j in m" :key="j">
          <img v-if="j == hero.posx.value && i == hero.posy.value" :src="heroImage[hero.orientation.value]" />
          <img v-else :src="images[0]" />
        </div>
      </div>
    </div>
  </div>
  <div class="msgBox">
    {{n}}
    {{m}}
    {{gameMsg}}
    {{money}}
  </div>
  <div class="pokeBox">
    <div class="pokemon" v-for="i in poke.length" :key="i">
      <img :src="images[poke[i-1]+4]" />
    </div>
  </div>
  
</template>

<script>
import { useStore } from 'vuex';
import { ref, computed } from "vue";
import mazeCreator from '@/assets/mazeCreator.js';
import minralPutter from '@/assets/minralPutter.js';
import gacha from "@/assets/gacha.js";
export default {
  setup() {
    const store = useStore();
    const n =computed(() => { return store.state.row});
    const m =computed(() => { return store.state.col});
    const t = ref(0);
    const money= ref(0);
    const poke=ref([]);
    const gameMsg=ref("");
    const map = ref(mazeCreator(m.value-2,n.value-2));
    const endPos=ref(minralPutter(map.value,m.value,n.value));
    const imgbackground = require("@/assets/BackGround.png");
    const images = [
      require("@/assets/TransparentPicture.png"),
      require("@/assets/Stone.png"),
      require("@/assets/Exit.png"),
      require("@/assets/Ball0.png"),
      require("@/assets/Ball1.png"),
      require("@/assets/Diglett.png"),
      require("@/assets/Dialga.png"),
      require("@/assets/Meowth.png")
    ];
    const heroImage = [
      require("@/assets/Hero10.png"),
      require("@/assets/Hero11.png"),
      require("@/assets/Hero12.png"),
      require("@/assets/Hero20.png"),
      require("@/assets/Hero21.png"),
      require("@/assets/Hero22.png"),
      require("@/assets/Hero30.png"),
      require("@/assets/Hero31.png"),
      require("@/assets/Hero32.png"),
      require("@/assets/Hero40.png"),
      require("@/assets/Hero41.png"),
      require("@/assets/Hero42.png")
    ];
    const hero = {
      orientation: ref(0),
      posx: ref(endPos.value[0][0]),
      posy: ref(endPos.value[0][1])
    };
    const mapBegin={
      posx: ref(endPos.value[0][0]),
      posy: ref(endPos.value[0][1])
    };
    const mapExit ={
      posx: ref(endPos.value[1][0]),
      posy: ref(endPos.value[1][1])
    };
    const update =()=>{
      if (n.value>m.value) store.commit("update",[n.value,m.value+2]);
      else store.commit("update",[n.value+2,m.value]);
      map.value=mazeCreator(m.value-2,n.value-2);
      endPos.value=minralPutter(map.value,m.value,n.value);
      hero.posx.value=endPos.value[0][0];
      hero.posy.value=endPos.value[0][1];
      hero.orientation.value=0;
      mapBegin.value={posx:endPos.value[0][0],posy:endPos.value[0][1]};
      mapExit.value={posx:endPos.value[1][0],posy:endPos.value[1][1]};
    };
    const handleGacha= ()=>{
      switch (gacha()) {
        case 0:
          money.value += 300;
          break;
        case 1:
          poke.value.push(1);
          break;
        case 2:
          poke.value.push(2);
          break;
        case 3:
          poke.value.push(3);
          break;
      }
    };
    const herospin = (event) => {
      switch (event.keyCode) {
        case 65:
          hero.orientation.value=6;
          break;
        case 87:
          hero.orientation.value=0;
          break;
        case 68:
          hero.orientation.value=9;
          break;
        case 83:
          hero.orientation.value=3;
          break;
      }
    }
    const heromove = (event) => {
      t.value=1-t.value;
      switch (event.keyCode) {
        case 65:
          hero.orientation.value = 7+t.value;
          switch ((map.value[hero.posx.value - 1][hero.posy.value - 1 - 1]))
          {
            case 0:
              hero.posy.value -= 1;
              break;
            case 2:
              hero.posy.value -= 1;
              map.value[hero.posx.value - 1][hero.posy.value - 1]=0;
              update();
              break;
            case 3:
              map.value[hero.posx.value - 1][hero.posy.value - 1 - 1]=4;
              handleGacha();
              break;
          }
          break;
        case 87:
          hero.orientation.value = 1+t.value;
          switch ((map.value[hero.posx.value - 1 - 1][hero.posy.value - 1]))
          {
            case 0:
              hero.posx.value -= 1;
              break;
            case 2:
              hero.posx.value -= 1;
              map.value[hero.posx.value - 1][hero.posy.value - 1]=0;
              update();
              break;
            case 3:
              map.value[hero.posx.value - 1 - 1][hero.posy.value - 1]=4;
              handleGacha();
              break;
          }
          break;
        case 68:
          hero.orientation.value = 10+t.value;
          switch ((map.value[hero.posx.value - 1][hero.posy.value - 1 + 1]))
          {
            case 0:
              hero.posy.value += 1;
              break;
            case 2:
              hero.posy.value += 1;
              map.value[hero.posx.value - 1][hero.posy.value - 1]=0;
              update();
              break;
            case 3:
              map.value[hero.posx.value - 1][hero.posy.value - 1+1]=4;
              handleGacha();
              break;
          }
          break;
        case 83:
          hero.orientation.value = 4+t.value;
          switch ((map.value[hero.posx.value - 1 + 1][hero.posy.value - 1]))
          {
            case 0:
              hero.posx.value += 1;
              break;
            case 2:
              hero.posx.value += 1;
              map.value[hero.posx.value - 1 ][hero.posy.value - 1]=0;
              update();
              break;
            case 3:
              map.value[hero.posx.value - 1 + 1][hero.posy.value - 1]=4;
              handleGacha();
              break;
          }
          break;
      }
    };
    return {
      n,
      m,
      t,
      map,
      store,
      gameMsg,
      imgbackground,
      images,
      mapExit,
      mapBegin,
      heroImage,
      hero,
      poke,
      money,
      endPos,
      heromove,
      update,
      herospin,
      handleGacha
    };
  },
};
</script>

<style scoped>
.container {
  position: absolute;
  display: flex;
  justify-content: center;
}
.msgBox{
  position: absolute;
  left:0px;
  background-color: white;
}
.box {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  height: 200vh;
  width: 100%;
  overflow: hidden;
}
.pokeBox{
  position: absolute;
  top:200px;
  left:0px;
}
.row {
  display: block;
}

.col {
  height: 32px;
}
.pokemon
{
  position: relative;
  top:75px;
  height:75px;
}
.banner {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>